.not-found-page__back {
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  margin: 185px 0 60px 0;

  text-align: center;

  color: #3456f3;
}
.not-found-page {
  display: flex;
  flex-direction: column;
}
.not-found-page__404 {
  font-weight: normal;
  font-size: 140px;
  line-height: 169px;
  text-align: center;
  margin: 280px 0 45px 0;
}
.not-found-page__text {
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  margin: 0;
}

@media screen and (max-width: 420px) {
  .not-found-page__404 {
    font-size: 80px;
    line-height: 97px;
  }
}
